<!--
    图表
    @params: width 宽度,默认100%
    @params: height 高度，默认350px
    @params: autoResize 是否自动调整大小,默认true
    @params: chartOption 图表的配置
-->
<template>
  <div ref="chart" :style="{ height: height, width: width }" />
</template>
<script>
// 引入 echarts 核心模块，核心模块提供了 echarts 使用必须要的接口。
import * as echarts from 'echarts/core'
import ElementResize from 'element-resize-detector'
// import resize from './mixins/resize'
// 引入图表，图表后缀都为 Chart
import { BarChart, LineChart, PieChart } from 'echarts/charts'
// 引入提示框，标题，直角坐标系等组件，组件后缀都为 Component
import {
  TitleComponent,
  TooltipComponent,
  GridComponent,
  LegendComponent,
  ToolboxComponent
} from 'echarts/components'
// 引入 Canvas 渲染器，注意引入 CanvasRenderer 或者 SVGRenderer 是必须的一步
import { CanvasRenderer } from 'echarts/renderers'

// 注册组件
echarts.use([
  TitleComponent,
  TooltipComponent,
  GridComponent,
  LegendComponent,
  ToolboxComponent,
  BarChart,
  LineChart,
  PieChart,
  CanvasRenderer
])

export default {
  // mixins: [resize],
  name: 'ChartView',
  props: {
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '350px'
    },
    chartOption: {
      type: Object,
      required: true
    },
    theme: {
      type: String,
      default: 'light'
    }
  },
  data () {
    return {
      // Echarts实例
      chart: null,
      ElementResizeInstance: null
    }
  },
  watch: {
    // 监听配置选项
    chartOption: {
      deep: true, // 对象深度监听
      handler (newVal) {
        this.setOptions(newVal)
      }
    },
    theme: {
      handler (newVal) {
        this.theme = newVal
        this.chart.dispose()
        this.initChart()
        console.log(this.chart)
      }
    }
  },
  mounted () {
    this.initChart()
  },
  beforeDestroy () {
    if (!this.chart) {
      return
    }
    window.removeEventListener('resize', this.windowResizeListener)
    /* 释放图表实例 */
    this.chart.dispose()
    /* dispose 会释放内部占用的一些资源和事件绑定，但是解除实例的引用我们是做不到的，所以需要重新赋值为null */
    this.chart = null
  },
  methods: {
    initChart () {
      /* 初始化echarts实例，第一个参数是dom，第二个是主题，默认为空(明亮)，第三个是渲染器，默认为canvas */
      this.chart = echarts.init(this.$refs.chart, this.theme)
      this.chart.setOption(this.chartOption)
      window.addEventListener('resize', this.windowResizeListener)
      this.EleResizeListener()
      // this.chart.on('click', this.handleClick)
    },

    /* 对chart元素尺寸进行监听，当发生变化时同步更新echart视图 */
    EleResizeListener () {
      this.ElementResizeInstance = ElementResize({
        strategy: 'scroll',
        callOnAdd: true
      })
      this.ElementResizeInstance.listenTo(this.$refs.chart, () => {
        this.$nextTick(() => {
          if (!this.chart) return
          this.chart.resize()
        })
      })
    },
    /* 当窗口缩放时，echart动态调整自身大小 */
    windowResizeListener () {
      if (!this.chart) return
      this.chart.resize()
    }
    // handleClick (params) {
    //   this.$emit('click', params)
    // },
    // setOptions (option) {
    //   this.clearChart()
    //   if (this.chart) {
    //     this.chart.setOption(option)
    //   }
    // }
    // refresh () {
    //   this.setOptions(this.chartOption)
    // },
    // clearChart () {
    //   this.chart && this.chart.clear()
    // }
  }
}
</script>
